<template>
  <!-- 注册界面 -->
  <cardcontent>
    <div class="row justify-content-center text-start">
      <div class="col-3">
        <form @submit.prevent="out">
  <div class="mb-3 mt-3">
    <label for="username" class="form-label">username:</label>
    <input v-model="username" type="text" class="form-control" id="username" placeholder="Enter username" name="username">
  </div>
  <div class="mb-3">
    <label for="password" class="form-label">Password:</label>
    <input v-model="password" type="password" class="form-control" id="password" placeholder="Enter password" name="password">
  </div>
  <div class="mb-3">
    <label for="repassword" class="form-label">again Password:</label>
    <input v-model="repassword" type="password" class="form-control" id="repassword" placeholder="again password" name="repassword">
  </div>
  <label class="form-label red">{{warn}}</label>
  <button type="submit" class="btn btn-primary w-100">Submit</button>
</form>
      </div>
    </div>
  </cardcontent>
</template>
<style scoped>
  .red{
    color: red;
  }
</style>
<script>
import { ref } from 'vue';
import cardcontent from '../components/cardcontent';
import router from '@/router';
import $ from 'jquery';
import { useStore } from 'vuex';
export default {
  name: 'HaveView',
  components: {
    cardcontent,
  },
  setup() {
  let password=ref('');
  let username=ref('');
  let warn=ref(' ');
  let repassword=ref('');
  let store=useStore();
  const out=()=>{
    $.ajax({
      type: "POST",
      url: "https://app165.acapp.acwing.com.cn/myspace/user/",
      data: {
        username:username.value,
        password:password.value,
        password_confirm:repassword.value,
      },
      success(resp){
        if(resp.result=="success"){
          store.dispatch("login",{
      // 传入login的参数
      password:password.value,
      username:username.value,
      success(){
        localStorage.setItem("username",password.value);
        localStorage.setItem("password",password.value);
        router.push({name:'friends'});//成功自动跳转到friends页面；
      },
      error(){
        router.push({name:'error'});
      }
    });
        }
        else if(resp.result=="用户名和密码不能为空")
        warn.value="用户名和密码不能为空";
        else if(resp.result=="两个密码不一致")
        warn.value="两个密码不一致";
        else if(resp.result=="用户名已存在")
        warn.value="用户名已存在";
      },
      error(){
        console.log("error");
      }
    });
  }
  return{
    password,
    username,
    warn,
    out,
    repassword,
  }
  }
}
</script>
