<template>
    <nav class="navbar navbar-expand-sm bg-light navbar-light">
  <div class="container-fluid">
    <ul class="navbar-nav">
      <li class="nav-item">
        <!-- 前端渲染router-link -->
        <router-link class="nav-link active" :to="{name:'home'}">首页</router-link>
      </li>
      <li class="nav-item">
        <router-link class="nav-link" :to="{name:'friends'}">好友列表</router-link>
      </li>
      <li class="nav-item">
        <!-- 传一个参数 -->
        <router-link class="nav-link" :to="{name:'user',params:{userid:$store.state.user.id}}">用户动态</router-link>
      </li>
    </ul>
    <ul class="navbar-nav" v-if="!$store.state.user.is_login">
      <li class="nav-item">
        <router-link class="nav-link" :to="{name:'login'}">登录</router-link>
      </li>
      <li class="nav-item">
        <router-link class="nav-link" :to="{name:'have'}">注册</router-link>
      </li>
    </ul>
    <ul class="navbar-nav" v-else>
      <li class="nav-item">
        <router-link class="nav-link" :to="{name:'user',params:{userid:$store.state.user.id}}">{{$store.state.user.username}}</router-link>
      </li>
      <li class="nav-item">
        <a class="nav-link" @click="back">退出</a>
      </li>
    </ul>
  </div>
</nav>
</template>

<script>
// import { ref } from 'vue';
  import { useStore } from 'vuex';
export default {
    name:"NavBar",
    setup(){
      // const id=ref(2);
      const store=useStore();
      const back=()=>{
        store.commit("back");
        location.reload();
      };
      return{
        back, 
      }
    }
}
</script>
<style scoped>
  a{
    cursor: pointer;
  }
</style>