<template>
  <!-- 登录界面 -->
  <cardcontent>
    <div class="row justify-content-center text-start">
      <div class="col-3">
        <form @submit.prevent="out">
  <div class="mb-3 mt-3">
    <label for="username" class="form-label">username:</label>
    <input v-model="username" type="text" class="form-control" id="username" placeholder="Enter username" name="username">
  </div>
  <div class="mb-3">
    <label for="password" class="form-label">Password:</label>
    <input v-model="password" type="password" class="form-control" id="password" placeholder="Enter password" name="password">
  </div>
  <button type="submit" class="btn btn-primary w-100">Submit</button>
</form>
      </div>
    </div>
  </cardcontent>
</template>
<style scoped>
</style>
<script>
import { ref } from 'vue';
import cardcontent from '../components/cardcontent';
import {useStore} from 'vuex';
import router from '@/router';
export default {
  name: 'LoginView',
  components: {
    cardcontent,
  },
  setup() {
    const store=useStore();
  let password=ref('');
  let username=ref('');
  // let login=ref('');
  const out=()=>{
    // dispatch调用actions的方法
    store.dispatch("login",{
      // 传入login的参数
      password:password.value,
      username:username.value,
      success(){
        localStorage.setItem("username",password.value);
        localStorage.setItem("password",password.value);
        router.push({name:'friends'});//成功自动跳转到friends页面；
      },
      error(){
        console.log("error");
      }
    });
  }
  return{
    password,
    username,
    // login,
    out,
  }
  }
}
</script>
