import $ from 'jquery';
import jwtDecode from 'jwt-decode';
const User = {
    state: { //存储基本对象
        id: -1,
        username: "",
        photo: "",
        followerCount: 0,
        access: "",
        refresh: "",
        is_login: false,
    },
    getters: { //存储一些需要计算后获得的数据
    },
    mutations: { //定义对state数据的修改操作
        updateUser(state, user) {
            state.id = user.id;
            state.username = user.username;
            state.photo = user.photo;
            state.followerCount = user.followerCount;
            state.access = user.access;
            state.refresh = user.refresh;
            state.is_login = user.is_login;
        },
        back(state) {
            state.id = "";
            state.username = "";
            state.photo = "";
            state.followerCount = 0;
            state.access = "";
            state.refresh = "";
            state.is_login = false;
        }
    },
    actions: { //定义对state数据的一些操作方式
        login(content, data) { //传入的api，data为自己传入的数据，如一些携带数据和成功失败的调用函数
            $.ajax({
                type: "POST",
                url: "https://app165.acapp.acwing.com.cn/api/token/",
                data: {
                    username: data.username,
                    password: data.password,
                },
                success(resp) {
                    const { access, refresh } = resp;
                    const access_user = jwtDecode(access);
                    $.ajax({
                        type: 'GET',
                        url: "https://app165.acapp.acwing.com.cn/myspace/getinfo/",
                        data: {
                            user_id: access_user.user_id,
                        },
                        headers: {
                            'Authorization': "Bearer " + access,
                        },
                        success(resp) {
                            // 调用mutations的方法
                            content.commit("updateUser", {
                                ...resp,
                                access: access,
                                refresh: refresh,
                                is_login: true,
                            });
                            data.success();
                        },
                        error() {
                            data.error();
                        }
                    })
                }
            })
        }
    },
    modules: {}
};
export default User;