<template>
    <div class="card">
        <div class="card-body">
            <div class="row">
        <div class="col-3">
            <img :src="users.photo" class="img-fluid" />
        </div>
        <div class="col-9 text-start">
            <h2>{{users.username}}</h2>
            <div class="fsize">粉丝：{{users.fans}}</div>
            <button v-if="!users.see" @click="follow(users.id)" class="btn btn-info btn-sm mt-2">+关注</button>
            <button v-if="users.see" @click="unfollow(users.id)" class="btn btn-info btn-sm mt-2">取消关注</button>
        </div>
    </div>
        </div>
    </div>
</template>
<script>
import { computed } from '@vue/runtime-core'

export default{
    emits:['follow','unfollow'],
    name:"UserName",
    props:{
        users:{
            type:Object,
            required:false,
        },
    },
    setup(props,content) {
        // computed方法可以进行计算
        let result=computed(()=>props.users.head+""+props.users.name);
        
        const follow=(id)=>{
            // 将事件传上父组件
            content.emit('follow',id);
        }
        const unfollow=(id)=>{
            content.emit('unfollow',id);
        }
        return {
            result,
            follow,
            unfollow,
        }
    }
}
</script>
<style scoped>
img{
    border-radius: 50%;
}
.fsize{
    font-size: 12px;
    color:darkgrey;
}
.btn{
    margin: 2px 4px;
}
</style>