<template>
  <cardcontent>
    <!-- 好友列表 -->
    <div class="card mt-2" v-for="item in users.arr" :key="item.id" @click="eventone(item.id)">
      <div class="card-body">
        <div class="row text-start">
          <div class="col-1"><img class="img-fluid" :src="item.photo" alt=""></div>
          <div class="col-11">
            <h3>{{item.username}}</h3>
            <div class="follow">{{item.followerCount}}</div>
            <!-- <button item.id></button> -->
          </div>
        </div>
      </div>
    </div>
  </cardcontent>
</template>
<style scoped>
.follow{
  color: gray;
  font-size: 12px;
}
.card:hover{
  box-shadow: 2px 2px 8px lightgray;
  transition: 500ms;
}
.card{
  cursor: pointer;
}
img{
  border-radius: 50%;
}
</style>
<script>
import cardcontent from '../components/cardcontent';
import $ from 'jquery';
import { reactive } from '@vue/reactivity';
import router from '@/router';//可用于跳转界面
import { useStore } from 'vuex';//全局变量
export default {
  name: 'FriendsView',
  components: {
    cardcontent,
  },
  setup() {
    const store=useStore();
    let users=reactive({
      arr:[]
      })
    $.ajax({
      type: "get",
      url: "https://app165.acapp.acwing.com.cn/myspace/userlist/",
      success: function (response) {
        users.arr=response;
      }
    });
    const eventone=(id)=>{
      console.log("执行");
      if(store.state.user.is_login)
      router.push({
        name:'user',
        params:{
          userid:id//参数名和变量名一致可只写一次
        },
      })
      else{
        router.push({
          name:'login',
        })
      }
    }
    return{
      users,
      eventone,
    }
  }
}
</script>
