<template>
    <label for="comment">动态发布</label>
<textarea v-model="text" class="form-control" rows="5" id="comment" name="text"></textarea>
<button class="btn btn-sm btn-primary mt-2" @click="send">发布</button>
</template>
<style scoped>

</style>
<script>
import { ref } from '@vue/reactivity'
import $ from 'jquery';
import { useStore } from 'vuex';
export default{
    emits:['send'],
    name:"ListSend",
    setup(props,content){
        const store=useStore();
        let text=ref('');
        const send=()=>{
            if(text.value!==""){
                $.ajax({
                type: "POST",
                url: "https://app165.acapp.acwing.com.cn/myspace/post/",
                data: {
                    content:text.value,
                },
                headers:{
                    'Authorization': "Bearer " +store.state.user.access,
                },
                success: function (response) {
                    if(response.result=="success"){
                        content.emit('send',text.value);//emit用于子组件调用父组件的方法
                        text.value="";
                    }
                }
            });
            }
        }
        return{
            send,
            text,
        }
    }
}
</script>
