<template>
    <div v-for="item in list.arr" :key="item.id">
        <div class="card mb-2">
            <div class="card-body text-start">
                {{item.content}}
                <button v-if="is_me" type="button" class="btn btn-danger btn-sm" @click="deletebyid(item.id)">删除</button>
            </div>
        </div>
    </div>
</template>
<style scoped>
    .btn-danger{
        float: right;
    }
</style>
<script>
import { computed } from 'vue';
import { useStore } from 'vuex';
export default {
    emits:['delete'],
    name:"ListContent",
    props:{
        list:{//上层组件传输来的参数
            type:Object,
            required:false,
        },
        user:{
            type:Object,
            required:true,
        }
    },
    setup(props,content){
        const store=useStore();
        const is_me=computed(()=>props.user.id==store.state.user.id);
        const deletebyid=(id)=>{
            content.emit("delete",id);
        }
        return{
            is_me,
            deletebyid,
        }
    }
}
</script>