import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue';
import friends from "../views/FriendsView";
import have from "../views/HaveView";
import login from "../views/LoginView";
import user from "../views/UserView";
import error from "../views/ErrorView";
const routes = [{
        path: '/',
        name: 'home',
        component: HomeView
    },
    {
        path: '/friends/',
        name: 'friends',
        component: friends
    },
    {
        path: '/have/',
        name: 'have',
        component: have
    },
    {
        path: '/login/',
        name: 'login',
        component: login
    },
    {
        // 需要传参，参数名为userid
        path: '/user/:userid/',
        name: 'user',
        component: user
    },
    {
        path: '/error/',
        name: 'error',
        component: error
    },
    {
        path: '/:catchAll(.*)',
        redirect: '/error/'
    },
]

const router = createRouter({
    history: createWebHistory(),
    routes
})

export default router