<template>
  <cardcontent>
    <div class="row">
      <div class="col-3">
        <!-- users为自定义属性，向子组件传递参数使用，follow与unfollow为自定义事件，类似与函数，接受子组件触发 -->
      <username :users="user"  @follow="follow" @unfollow="unfollow"></username>
      <send v-if="is_self" @send="send" />
      </div>
      <div class="col-9">
        <list :user="user" :list="list" @delete="delete_listcontent"></list>
      </div>
    </div>
  </cardcontent>
</template>

<script>
import cardcontent from '../components/cardcontent';
import username from '../components/UserName';
import list from '../components/ListContent';
import {computed, reactive} from 'vue';
import send from '../components/ListSend';
import { useStore } from 'vuex';
import { useRoute } from 'vue-router';
import $ from 'jquery';
// 获取参数的api
// import { useRoute } from 'vue-router';
export default {
  name: 'UserView',
  components: {
    cardcontent,
    username,
    list,
    send,
  },
  // 父组件向子组件传递数据
  setup() {
    // 获取参数
    // let par=useRoute();
    // console.log(par.params.userid);
    const store=useStore();
    const route=useRoute();
    const id=route.params.userid;
    const user=reactive({//不可更改数据,类似final，但是数组可以修改
      id:1,
      username:"未登录",
      head:"赵",
      name:"普",
      fans:0,
      see:false,
      photo:"",
    });
    const is_self=computed(()=>id==store.state.user.id)
    const list=reactive({
      num:2,
      arr:[{
        id:1,
        content:"今天又是快乐的一天呢。"
      },{
        id:2,
        content:"今天又是悲伤的一天呢。"
      }
    ]});
    $.ajax({
      type: "GET",
      url: "https://app165.acapp.acwing.com.cn/myspace/getinfo/",
      data: {
        user_id:id,
      },
      headers:{
        'Authorization': "Bearer " +store.state.user.access,
      },
      success: function (response) {
        user.id=response.id;
        user.username=response.username;
        user.fans=response.followerCount;
        user.see=response.is_followed;
        user.photo=response.photo;7
      },
      error(){
        console.log("错误");
      }
    }),
    $.ajax({
      type: "GET",
      url: "https://app165.acapp.acwing.com.cn/myspace/post/",
      data: {
        user_id:id,
      },
      headers:{
        'Authorization': "Bearer " +store.state.user.access,
      },
      success: function (response) {
        list.num=response.length;
        list.arr=response;
      }
    });
    const delete_listcontent=(id)=>{
      $.ajax({
        type: "DELETE",
        url: "https://app165.acapp.acwing.com.cn/myspace/post/",
        data: {
          post_id:id,
        },
        headers:{
        'Authorization': "Bearer " +store.state.user.access,
        },
        success: function (response) {
          if(response.result=="success"){
            list.arr=list.arr.filter(arr=>arr.id!=id);
            console.log(list.arr);
          list.num=list.arr.length;
          }
        }
      });
    }
    const send=(text)=>{
      console.log(list.arr);
      list.num++;
      list.arr.unshift({id:list.num,content:text});//前插入
    }
    const follow=(id)=>{
      user.fans++;
      user.see=true;
      target(id);
    }
    const unfollow=(id)=>{
      user.fans--;
      user.see=false;
      target(id);
    }
    const target=(id)=>{
      $.ajax({
        type: "POST",
        url: "https://app165.acapp.acwing.com.cn/myspace/follow/",
        data: {
          target_id:id,
        },
        headers:{
        'Authorization': "Bearer " +store.state.user.access,
        },
        success: function (response) {
          console.log(response.result);
        }
      });
    }
    return {
      user,
      follow,
      unfollow,
      list,
      send,
      is_self,
      delete_listcontent,
    }
  }
}
</script>
